body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.main {
  position:relative;
  background-color: #f5f8fd;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  color: #000;
}

.topic {
  display: grid;
  flex-direction: row;
  grid-template-areas:
    'leftTitle rightTitle'
    'pTitle pTitle';
}

.leftTitle {
  float:left;
  padding-top: 3vmin;
  grid-area: leftTitle;
  padding-left:18vmin;
  font-size: 4vmin;
  font-weight: 740;
  z-index: 5;
}

.shape {
  float:right;
  grid-area: rightTitle;
}


.rectangle {
  position:relative;
  height: 9vmin;
  width: 26vmin;
  margin-left:2px;
  margin-bottom: 5vmin;
  padding-top: 2vmin;
  background-color: #78cc1f;
  z-index: 1;
}

.emptyRectangle {
  position:relative;
  height: 9.2vmin;
  width: 26.8vmin;
  margin-left:2vmin;
  border:0.8vmin solid #fff;
  z-index: 1;
}

.rightTitle {
  position:absolute;
  font-size: 4vmin;
  font-weight: 740;
  top:0.1vmin;
  margin-left:2.7vmin;
  z-index: 50;
}

.pTitle {
  grid-area: pTitle;
  font-size: 3.5vmin;
  padding-left:22vmin;
}

.buttons {
  position:absolute;
  align-self:center;
  top:77vh;
  flex-direction: row;
  display:inline;
}

.studentButton{
  height:8vmin;
  width:22vmin;
  border:0px;
  font-size:3.5vmin;
  color:#fff;
  box-shadow:rgba(0, 0, 0, 0.2) -0.5vmin 0.7vmin 0.6vmin 0px;
  background-color:#78cc1f;
}

.companyButton{
  height:8vmin;
  width:22vmin;
  border:0px;
  font-size:3.5vmin;
  color:#fff;
  box-shadow: rgba(0, 0, 0, 0.2) -0.5vmin 0.7vmin 0.6vmin 0px;
  background-color: #404040;
}

@media only screen and (max-width: 767.9px)  {

  .main {
    min-height: 93vh;
  }

  .buttons {
    position:absolute;
    top:auto;
    bottom: 0vh;
    display:flex;
  }
  
  .companyButton{
    float:left;
    height:10vh;
    width:50vw;
    border:0px;
    font-size:5vmin;
    color:#fff;
    box-shadow: rgba(0, 0, 0, 0.2) -0.5vmin 0.7vmin 0.6vmin 0px;
    background-color: #404040;
  }
  
  .studentButton{
    float:right;
    height:10vh;
    width:50vw;
    border:0px;
    font-size:5vmin;
    color:#fff;
    box-shadow:rgba(0, 0, 0, 0.2) -0.5vmin 0.7vmin 0.6vmin 0px;
    background-color:#78cc1f;
  }

}

/* mobile responsive buttons */
@media only screen and (max-height: 830px) and (max-width: 767.9px) {
  .main {
    min-height: 92.7vmax;
  }
}
@media only screen and (max-height: 737px) and (max-width: 767.9px) {
  .main {
    min-height: 91.7vmax;
  }
}
@media only screen and (max-height: 668px) and (max-width: 767.9px) {
  .main {
    min-height: 91vmax;
  }
}
@media only screen and (max-height: 641px) and (max-width: 767.9px) {
  .main {
    min-height: 90.5vmax;
  }
}
@media only screen and (max-height: 569px) and (max-width: 767.9px) {
  .main {
    min-height: 89.5vmax;
  }
} 
.nav{
    background-color:#fff;
    position: fixed;
    top:0;
    width: 76vw;
    height: 10vh;
    margin: 0px 12vw;
    border-radius: 0 0 17px 17px;
    z-index:100;
}

.leftNav{
    padding-left:10px;
}

.rightNav{
    padding-right:10px;
}

.link{
    white-space: nowrap;
    text-align: center;
    vertical-align: middle;
    line-height: 7vh; 
}

.cont{
    width: 11vw;
    margin: 0 0.2vw 10px 0.2vw;
    white-space: nowrap;
}

.comp{
    background-color: #404040;
}

.stud{
    background-color: #78cc1f;
}

.link:hover{
    background-color:#78cc1f; 
}

.link:hover span{
    color: #fff;
}

.link span{
    color:#a1a1a1;
    font-size: 1rem;
    margin: 0 1.5vw;
}


.navName{ 
    display: none; 
}

.staticNav{
    color: #fff;
    font-size: 1.2vw;
    text-align: center;
    vertical-align: middle;
    line-height: 7.65vh; 
    font-weight: 400;
    margin-top: 3px;
}


@media only screen and (max-width: 1400px) {
    .nav{
        width:84%;
        margin: 0px 8vw;
    }
}

@media only screen and (max-width: 1150px) {
    .nav{
        width:100%;
        margin: 0;
    }
}


@media only screen and (max-width: 767.9px) {

    .nav{
        position: relative;
        height: auto;  
    }

    .leftNav{
        padding: 0px;
    }
    
    .rightNav{
        padding: 0px;
    }
    
    .navName{
        color:#000;
        font-size: 5vw;
        margin-left: 7vw;
        display: block;
    }

    .hideBlock{
        display:none;
    }

    .link{
        border-width: 0px 0px 2px 0px;
        border-style: solid;
        border-color: #f5f8fd;
        text-align: left;
        text-align: initial;
        vertical-align: baseline;
        vertical-align: initial;
        line-height: normal;
        line-height: initial; 
    }

    .link span{
        color:#000;
        font-size: 3vmin;
        margin-left:9vw;
    }

    .navButton{
        background-color:#78cc1f;
        border:0px;
        border-radius:0px;
        height:60px;
        width:75px;
        margin-top:0vh;
        margin-left:4vw;
    }
    
}
